.not-found {
  display: flex;
  height: 100vh;
  align-items: center;
  justify-content: center;
  text-align: center;


  h2 {
    font-size: 4rem;
    padding: 2rem;
    line-height: .4rem;

    &:after {
      content: '';
      height: 500px;
      width: 500px;
      background: $color-3;
      border-radius: 50%;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      z-index: -1;
    }
  }


  h3 {
    font-size: 2em;
    transition: .4s;
    opacity: .8;

    &:hover {
      transform: scale(1.3);
      opacity: 1;
    }
  }
}
