.navigation {
  position: absolute;
  top: 1rem;
  left: 1.4rem;
  font-size: 1.4rem;
  line-height: 28px;
  color: $color-4;

  &:before {
    content: '';
    background: $color-3;
    width: 300px;
    height: 300px;
    border-radius: 50%;
    position: absolute;
    top: -150px;
    left: -150px;
    z-index: -1;
    animation: navAnim 15s infinite alternate;

    @keyframes navAnim {
      50% {
        transform: scale(1.2);
      }

      100% {
        transform: scale(1);
      }
    }

    @media screen and (max-width: 430px) {
      display: none;
    }
  }

  .nav-active {
    color: $black;
  }

  ul:hover li:not(:hover) {
    opacity: .5;
  }

  ul:hover li:hover {
    color: $black;
  }

  .nav-portfolio {
    cursor: pointer;
    display: flex;
    transition: .3s;

    &:hover .nav-projects,
    .nav-projects:hover {

      visibility: visible;
      opacity: 1;
    }

    .nav-projects {
      position: absolute;
      visibility: hidden;
      opacity: 0;
      padding: 0 40px;
      width: 200px;
      transform: translateX(70px);
      transition: .3s;
      perspective: 500px;

      li {
        transform: rotateY(30deg);
        transform-origin: center;
        transition: .3s;
      }
    }
  }
}
