.cursor {
  width: 40px;
  height: 40px;
  border: 1px solid $color-2;
  position: absolute;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  pointer-events: none;
  transition: 2ms ease-out;
  z-index: 2;
  backdrop-filter: brightness(115%);

  &.hovered {
    width: 30px;
    height: 30px;
  }
}
